<template>
  <div>
    <a-row v-for="(row, key) in items" :key="key" :gutter="[24]">
      <a-col
        :sm="{ span: input.sm ? input.sm : 24 }"
        :md="{ span: input.md ? input.md : 12 }"
        :xl="{ span: input.xl ? input.xl : 8 }"
        :span="24"
        v-for="input in row"
        :key="input.name"
      >
        <x-input
          v-if="!input.mask && !input.disabled"
          :label="!getVisible(input.visible) ? '' : getLabel(input)"
          :ref="input.name"
          :type="!getVisible(input.visible) ? 'hidden' : input.type"
          :name="input.name"
          :max="input.max"
          :min="input.min"
          :maxlength="input.maxlength"
          :validation="!getVisible(input.visible) ? '' : input.validation"
          :options="getOptions(input)"
          :multiple="input.multiple"
          :selectable="input.selectable"
          :class="getClass(input)"
          error-behavior="submit"
        >
        </x-input>
        <x-input
          v-if="input.mask"
          :label="input.label"
          :type="input.type"
          :name="input.name"
          v-mask="input.mask"
          :validation="input.validation"
          :options="getOptions(input)"
          :multiple="input.multiple"
          :selectable="input.selectable"
          error-behavior="submit"
        >
        </x-input>
        <x-input
          v-if="input.disabled"
          :label="input.label"
          :type="input.type"
          :name="input.name"
          :class="input.class"
          disabled
          error-behavior="submit"
        ></x-input>
      </a-col>
    </a-row>
  </div>
</template>

<script>
import { mask } from 'vue-the-mask'

export default {
  directives: { mask },
  props: {
    items: {
      type: Array,
      required: true
    },
  },
  methods: {
    getVisible(cb) {
      return cb ? cb() : true
    },
    getClass(input) {
      return typeof input.class == 'function' ? input.class() : input.class
    },
    getOptions(input) {
      return input.optionName ? this.$store.getters[input.optionName] : typeof input.options == 'function' ? input.options() : input.options
    },
    getLabel(input) {
      return typeof input.label == 'function' ? input.label() : input.label
    }
  }
}
</script>

