<template>
  <div class="container">
    <a-row :gutter="[24]" v-if="userInfo">
      <a-col
        :xs="{ span: 24, offset: 0 }"
        :md="{ span: 22, offset: 1 }"
        :lg="{ span: 22, offset: 1 }"
        :xl="{ span: 22, offset: 1 }"
        class="center"
      >
        <div class="container-title">
          <h2 class="page-title">Редактирование</h2>
          <component-tabs></component-tabs>
        </div>
        <x-form
          v-if="!loading"
          v-model="form"
          :values="form"
          @submit="saveForm"
          class="profile-application-container"
        >
          <upload-photo></upload-photo>
          <div class="profile-form">
            <span class="prompt">*-поля, обязательные для заполнения</span>
            <application-form :items="items"></application-form>
          </div>
          <change-password></change-password>
          <a-col
            :md="{ span: 24, offset: 0 }"
            :xl="{ span: 14, offset: 5 }"
            :lg="{ span: 24, offset: 0 }"
            :xs="24"
            class="inline-buttons"
          >
            <a-button
              :disabled="loading"
              type="info"
              block
              html-type="submit"
              class="login-form-button"
              @onSubmit="toNextStep('back')"
            >
              Сохранить и выйти
            </a-button>
            <a-button
              :disabled="loading"
              type="primary"
              block
              html-type="submit"
              class="login-form-button"
              @onSubmit="toNextStep('next')"
            >
              Сохранить и продолжить
            </a-button>
          </a-col>
        </x-form>
        <a-skeleton active v-else :paragraph="{ rows: 16 }" />
      </a-col>
    </a-row>
  </div>
</template>

<script>
import ProfileSteps from '@/components/Student/ProfileSteps.vue'
import ProfileNomination from '@/components/Student/ProfileNomination.vue'
import ProfileInfoblock from '@/components/Student/ProfileInfoblock.vue'
import ApplicationForm from '@/components/Student/ApplicationForm.vue'
import ComponentTabs from '@/components/Student/ComponentTabs.vue'
import moment from 'moment'
import UploadPhoto from '@/components/Student/UploadPhoto'
import ChangePassword from '@/components/Student/ChangePassword'
import {
  from,
  cities,
  course_education,
  educationForm,
  educationPlace,
  documentView,
} from '@/helpers/select-data'
import cloneDeep from 'lodash.clonedeep'
import userMixin from '@/mixins/user.mixin.js'
export default {
  components: {
    UploadPhoto,
    ProfileSteps,
    ProfileNomination,
    ProfileInfoblock,
    ApplicationForm,
    ComponentTabs,
    ChangePassword,
  },
  mixins: [userMixin],
  data() {
    return {
      loading: true,
      educationForm: educationForm,
      form: {},
    }
  },
  async created() {
    console.log(this.items)
    await this.getNominations()
    this.form = cloneDeep(this.userInfo)
    this.renderForm()
    this.loading = false
  },
  methods: {
    getGender(gender) {
      return gender === 'male' ? 'Мужской' : 'Женский'
    },
    getBirthday(date) {
      return date.split('-').reverse().join('-')
    },
    renderForm() {
      console.log(this.form)
      this.form.citizenshipCheckbox = this.form.citizenship != 'РФ' ? 'Другое' : 'РФ' 
      if (!this.educationForm.some(form => form === this.form.form_education)) {
        this.form.form_education_another = this.form.form_education
        this.form.form_education = 'Другая'
      }
      this.form.nomination = this.form.nomination.map(item => {
        return `${item.id}`
      })
    },
    async getNominations() {
      try {
        await this.$store.dispatch('app/getEntities', {
          entityName: 'nomination',
        })
      } catch (e) {
        console.log(e)
      }
    },
    async saveForm() {
      this.loading = true
      try {
        console.log(this.form)
        const data = cloneDeep({
          ...this.form,
          form_education: this.form.form_education_another ? this.form.form_education_another : this.form.form_education,
          citizenship: this.form.citizenshipCheckbox === 'РФ' ? 'РФ' : this.form.citizenship,
        })
        delete data.user_avatar

        await this.$store.dispatch('app/saveEntity', {
          entityName: 'users',
          data,
          method: 'patch',
          id: data.id
        })
        await this.$store.dispatch('user/getUserData')
        this.$toast.success(`Успешно сохранено`)

        this.$router.push(`/profile`)
      } catch (e) {
        console.log(e)
        if (e?.response?.status === 400) {
          this.$errors.show(e.response.data)
          this.step = 'main'
        } else {
          this.$errors.showOnce('Ошибка входа')
        }
      } finally {
        this.loading = false
      }
    },
  },
  computed: {
    userInfo() {
      return this.$store.state.user.userInfo
    },
    getFullName() {
      const { first_name, last_name, patronymic } = this.userInfo
      return `${first_name} ${last_name} ${patronymic}`
    },
    getAge() {
      return moment().diff(this.userInfo.date_birth, 'years')
    },
    activePage() {
      return this.$route.meta.tabName
    },
  },
}
</script>

