<template>
  <div class="photo-upload-container">
    <a-upload
      name="avatar1"
      list-type="picture-card"
      class="avatar-uploader"
      :show-upload-list="false"
      accept="image/jpeg,image/png,image/heic"
      :before-upload="beforeUpload"
      action="/api/v1/users/999999"
      @change="handleChange"
    >
      <img v-if="avatarSrc" :src="avatarSrc" alt="avatar" />
      <div v-else>
        <v-icon name="camera" />
      </div>
    </a-upload>
    <div class="help-text">Нажмите, чтобы прикрепить (до 5МБ)</div>
  </div>
</template>

<script>
function getBase64(img, callback) {
  const reader = new FileReader()
  reader.addEventListener('load', () => callback(reader.result))
  reader.readAsDataURL(img)
}
export default {
  props: {
    avatarSrc: {
      type: String,
      required: false,
    },
    userInfo: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      loading: false,
      fileList: [],
    }
  },
  methods: {
    handleChange(info) {
      console.log(info.file.status)
      if (info.file.status === 'uploading') {
        this.loading = true
        return
      }
      //из-за того что ant-upload автоматом делает post запрос, приходится в error делать все операции
      if (info.file.status === 'error') {
        getBase64(info.file.originFileObj, async imageUrl => {
          const formData = new FormData()
          formData.append('user_avatar', info.file.originFileObj)
          try {
            const response = await this.saveEntity(formData)
            this.$store.commit('user/SET_USER_INFO', response)
            console.log(this.avatarSrc)
            this.$toast.success('Успешно сохранено')
          } catch (e) {
            console.log(e)
          } finally {
            this.loading = false
          }
        })
      }
    },
    beforeUpload(file) {
      const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png' || file.type === 'image/heic' || file.type === 'image/heif'
      if (!isJpgOrPng) {
        this.$toast.error('Некорректный формат')
      }
      const isLt2M = file.size / 1024 / 1024 < 5
      if (!isLt2M) {
        this.$toast.error('Фото должно быть меньше 5мб')
      }
      return isJpgOrPng && isLt2M
    },
    async saveEntity(data) {
      try {
        return this.$store.dispatch('app/saveEntity', {
          data,
          method: 'patch',
          entityName: 'users',
          id: this.userInfo.id,
        })
      } catch (e) {
        throw e
      }
    },
  },
}
</script>
