<template>
  <a-tabs
    :tab-position="getWidth < 920 ? 'right' : 'top'"
    :default-active-key="activeTab ? activeTab : activePage"
    @change="key => activeTab ? $emit('setActiveNomination', key) : $router.push(`/profile/${key}`)"
  >
    <a-tab-pane v-for="tab in tabs ? tabs : routerTabs" :key="tab.id" :tab="tab.name"></a-tab-pane>
  </a-tabs>
</template>

<script>
import resize from '@/mixins/resize.mixin'

export default {
  mixins: [ resize ],
  props: {
    tabs: {
      type: Array,
    },
    activeTab: {
      type: Number | null,
    },
  },
  data() {
    return {
      routerTabs: [
        { name: 'Основные сведения', id: 'edit' },
        // { name: 'Анкета участника', id: 'questionnaire' },
        { name: 'Прикрепленные документы', id: 'achievements' },
      ],
    }
  },
  computed: {
    activePage() {
      return this.$route.meta.tabName
    }
  },
}
</script>

